import React from 'react';
import PropTypes from 'prop-types';
import PaddedDecimal from '../common/PaddedDecimal/PaddedDecimal';
import { truncateToDecimals } from '../../helpers/numberFormatter';
import { isFirefox } from '../../helpers/browserHelper';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import CancelAPButton from '../common/CancelAPButton';
import Spinner from '../common/Spinner/Spinner';

const orderBookClasses = getBEMClasses('orderbook');
const flexTable = getBEMClasses('flex-table');

const Level2DataTableComponent = props => {
  const {
    cancelOrders,
    decimalPlaces,
    onPriceClick,
    onQuantityClick,
    level2Data,
    classModifier,
    fetching
  } = props;

  if (fetching) {
    return (
      <Spinner customClass={orderBookClasses} classModifiers={classModifier} />
    );
  } else {
    return level2Data.map((data, index) => (
      <div
        key={index}
        className={`${flexTable('row')} ${orderBookClasses(
          'order-row',
          classModifier
        )}`}>
        <div
          onClick={() =>
            onPriceClick(
              truncateToDecimals(data.Price, decimalPlaces[data.Price.symbol])
            )
          }
          className={`${flexTable('column')} ${orderBookClasses(
            'table-price',
            classModifier
          )}`}>
          <PaddedDecimal
            bemMod={classModifier}
            value={data.Price}
            decimals={decimalPlaces[data.Price.symbol]}
          />
        </div>

        <div
          onClick={e =>
            onQuantityClick(
              truncateToDecimals(
                data.Quantity,
                decimalPlaces[data.Quantity.symbol]
              ),
              index,
              level2Data
            )
          }
          className={`${flexTable('column')} ${orderBookClasses(
            'table-qty',
            classModifier
          )}`}>
          <PaddedDecimal
            value={data.Quantity}
            decimals={decimalPlaces[data.Quantity.symbol]}
          />
        </div>

        <div
          className={`${flexTable('column')} ${orderBookClasses(
            'table-my-size',
            isFirefox() ? 'firefox' : ''
          )}`}>
          {data.MySize && (
            <span>
              <PaddedDecimal
                value={data.MySize}
                decimals={decimalPlaces[data.Quantity.symbol]}
              />
            </span>
          )}
        </div>
        <div
          className={`${flexTable('column')} ${orderBookClasses(
            'cancel-btn'
          )}`}>
          {data.MySize && (
            <CancelAPButton
              onClick={() => {
                if (data.OrderIds.length) {
                  cancelOrders(data.OrderIds);
                } else {
                  cancelOrders([data.OrderId]);
                }
              }}
              text=""
              customClass={orderBookClasses()}
            />
          )}
        </div>
      </div>
    ));
  }
};

Level2DataTableComponent.propTypes = {
  cancelOrders: PropTypes.func.isRequired,
  decimalPlaces: PropTypes.object.isRequired,
  onPriceClick: PropTypes.func.isRequired,
  onQuantityClick: PropTypes.func.isRequired,
  level2Data: PropTypes.array.isRequired,
  classModifier: PropTypes.string,
  fetching: PropTypes.bool
};

export default Level2DataTableComponent;



