import React from 'react';
import withAuthentication from '../../hocs/withAuthentication';
import resize from '../../hocs/resize';
import InstrumentRowComponent from '../../components/InstrumentRow/InstrumentRowComponent';
import OrderBookContainer from '../../components/OrderBookComponents/OrderBookContainer';
import PriceChartContainer from '../../components/PriceChart/PriceChartContainer';
import DepthChartContainer from '../../components/DepthChart/DepthChartContainer';
import RecentTradesContainer from '../../components/RecentTrades/RecentTradesContainer';
import OrderEntryContainer from '../../components/OrderEntryComponents/OrderEntryContainer';
import InstrumentPositionsContainer from '../../components/InstrumentPositions/InstrumentPositionsContainer';
import ConfirmLimitOrder from '../../components/ConfirmLimitOrderModal/ConfirmLimitOrderModalContainer';
import OrderHistoryContainer from '../../components/OrderHistoryComponents/OrderHistoryContainer';
import TradingLayoutContent768Px from '../Responsive/TrandingLayout/TradingLayoutContent768Px';
import TradingLayoutContent1280Px from '../Responsive/TrandingLayout/TradingLayoutContent1280Px';
import TradingLayoutContent1440Px from '../Responsive/TrandingLayout/TradingLayoutContent1440Px';
import TradingLayoutCharts1280Px from '../Responsive/TrandingLayout/TradingLayoutCharts1280Px';
import config from '../../config';
import { getBEMClasses } from '../../helpers/cssClassesHelper';

import './TradingLayout.css';

const tradingLayoutClasses = getBEMClasses('trading-layout');

class TradingLayout extends React.Component {
  renderContent = () => {
    let content;
    const props = {
      orderBook: OrderBookContainer,
      orderEntry: OrderEntryContainer,
      instrumentPositions: InstrumentPositionsContainer,
      recentTrades: RecentTradesContainer
    };

    const short = this.props.height <= 1080;

    if (this.props.width <= 968) {
      content = <TradingLayoutContent768Px {...props} />;
    } else if (this.props.width > 968 && this.props.width <= 1280) {
      content = <TradingLayoutContent1280Px short={short} {...props} />;
    } else {
      content = <TradingLayoutContent1440Px short={short} {...props} />;
    }

    return content;
  };

  renderCharts = () => {
    const props = {
      priceChart: PriceChartContainer,
      depthChart: DepthChartContainer,
      showDepthChart: config.TradingLayout.showDepthChart
    };

    if (this.props.width <= 1280) {
      return <TradingLayoutCharts1280Px {...props} />;
    }

    return (
      <React.Fragment>
        <PriceChartContainer />
        {config.TradingLayout.showDepthChart && <DepthChartContainer />}
      </React.Fragment>
    );
  };

  render() {
    return (
      <div className={tradingLayoutClasses('container')}>
        <InstrumentRowComponent />
        <div className={tradingLayoutClasses('body')}>
          <div className={tradingLayoutClasses('chart-container')}>
            {this.renderCharts()}
            <OrderHistoryContainer filterMode="selectedInstrument" />
          </div>
          {this.renderContent()}
        </div>
        <ConfirmLimitOrder />
      </div>
    );
  }
}

export default withAuthentication(resize(TradingLayout));



