import { connect } from 'react-redux';
import TVChartComponent from './TVChartComponent';
import { changeTickersInterval } from '../../redux/actions/tickersActions';
import { selectedInstrumentSelector } from '../../redux/selectors/instrumentPositionSelectors';

const mapStateToProps = state => {
  const selectedInstrument = selectedInstrumentSelector(state);
  const { decimalPlaces } = state.product;

  return { selectedInstrument, decimalPlaces };
};

const mapDispatchToProps = {
  changeTickersInterval
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TVChartComponent);



