import React from 'react';
import PropTypes from 'prop-types';
import Modal from '../common/Modal/Modal';
import APLabelWithText from '../common/APLabelWithText/APLabelWithText';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import './ConfirmLimitOrderModalComponent.css';

const bemClasses = getBEMClasses('limit-order-confirmation');

const ConfirmLimitOrderModalComponent = (props, context) => {
  const {
    formObj,
    isOpen,
    orderFormType,
    close,
    confirmOrder,
    limitPrice,
    marketPrice,
    priceSymbol
  } = props;

  return (
    <Modal
      isOpen={isOpen}
      title={context.t('Confirm Your Limit Order')}
      close={close}
      footer={{
        buttonText: context.t('Confirm Order'),
        onClick: () => confirmOrder(orderFormType, formObj)
      }}
      customClass={bemClasses('modal')}>
      <div className={bemClasses()}>
        <h3>{context.t('Please, confirm your Limir Order')}</h3>

        <div className={bemClasses('text')}>
          {context.t(
            'You trying to place order with `Limit price` below market price'
          )}
        </div>

        <APLabelWithText
          label={context.t(`Your Limit Price:`)}
          text={context.t(`${limitPrice} ${priceSymbol}`)}
        />

        <APLabelWithText
          label={context.t(`Market Price:`)}
          text={context.t(`${marketPrice} ${priceSymbol}`)}
        />

        <div className={bemClasses('text')}>
          {context.t('Please, confirm your order or exit')}
        </div>
      </div>
    </Modal>
  );
};

ConfirmLimitOrderModalComponent.propTypes = {
  close: PropTypes.func.isRequired,
  isOpen: PropTypes.bool.isRequired,
  confirmOrder: PropTypes.func.isRequired,
  limitPrice: PropTypes.string.isRequired,
  marketPrice: PropTypes.string.isRequired,
  priceSymbol: PropTypes.string.isRequired,
  orderFormType: PropTypes.string.isRequired,
  formObj: PropTypes.object.isRequired
};

ConfirmLimitOrderModalComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default ConfirmLimitOrderModalComponent;



