import { connect } from 'react-redux';
import modalDecorator from '../../hocs/modalDecorator';
import { MODAL_TYPES } from '../../redux/actions/modalActions';
import { orderFormTypes } from '../../constants/sendOrder/orderFormTypes';
import {
  placeOrder,
  clearOrderForm
} from '../../redux/actions/orderEntryActions';
import ConfirmLimitOrderModalComponent from './ConfirmLimitOrderModalComponent';
import { selectedInstrumentSelector } from '../../redux/selectors/instrumentPositionSelectors';

const mapStateToProps = (state, modalProps) => {
  const { form, level1, instrument } = state || {};
  const orderFormType = modalProps.orderFormType || orderFormTypes.default;
  let formObj = {};
  if (orderFormType === orderFormTypes.advanced) {
    formObj = form.advancedOrderEntry.values;
  } else if (orderFormType === orderFormTypes.default) {
    formObj = form.orderEntry.values;
  }

  const selectedInstrument = selectedInstrumentSelector(state);
  const marketPrice = Object.keys(level1).length
    ? level1[instrument.selectedInstrumentId.toString()].BestOffer.toString()
    : '0';
  return {
    orderFormType,
    formObj,
    limitPrice: formObj.limitPrice.toString(),
    marketPrice,
    priceSymbol: selectedInstrument.Product2Symbol
  };
};

const mapDispatchToProps = dispatch => {
  return {
    confirmOrder: (orderFormType, form) =>
      dispatch(placeOrder(orderFormType, form)).then(() => {
        if (orderFormType === orderFormTypes.advanced) {
          dispatch(clearOrderForm('advancedOrderEntry'));
        } else {
          dispatch(clearOrderForm('orderEntry'));
        }
      })
  };
};

const container = connect(
  mapStateToProps,
  mapDispatchToProps
)(ConfirmLimitOrderModalComponent);

export default modalDecorator({
  name: MODAL_TYPES.SUBMIT_LIMIT_ORDER
})(container);



